.FeedId__wrapper {
  margin: 40px;
  width: 640px;
}

.FeedId__number {
  text-align: center;
}

.FeedId__status {
  color: #00CCCC;
}

.FeedId__list {
  overflow-y: scroll;
  overflow-x: hidden;
  list-style-type: none;
  max-height: 366px;
  padding: 0;
  margin: 0;
}

.FeedId__item {
  display: flex;
  justify-content: space-between;
}

.FeedId__img {
  width: 64px;
  height: 64px;
  border: 3px solid #801ab2;
  border-radius: 50%;
  object-fit: contain;
  margin-right: 16px;
}

.img__wrapper {
  display: flex;
  align-items: center;
}

.price__wrapper {
  display: flex;
  align-items: center;
}

.finalyPrice__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}